/* General Reset and Box Model */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body and General Layout */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f9;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  padding: 20px;
}

#content {
  display: flex;
  width: 100%;
  max-width: 1200px;
  margin-top: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Sidebar Styling */
#side-bar {
  width: 250px;
  padding: 20px;
  background-color: #2c3e50;
  color: white;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Task Content Area */
#taskcontent {
  flex: 1;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin-left: 20px;
  overflow-y: auto;
  height: 80vh;
}

/* Sidebar Button Styling */
button {
  width: 100%;
  padding: 12px;
  margin: 8px 0;
  background-color: #2980b9;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #3498db;
}

/* Dialog Styling for Add and Update Tasks */
dialog {
  padding: 20px;
  border: none;
  border-radius: 10px;
  background-color: #ecf0f1;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
}

form input,
form button {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 16px;
}

form button {
  background-color: #2ecc71;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: #27ae60;
}

/* Task Display */
.task {
  padding: 20px;
  background-color: #ecf0f1;
  margin-bottom: 15px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.task:hover {
  background-color: #f0f3f4;
}

.task h3 {
  font-size: 22px;
  margin-bottom: 8px;
  color: #2c3e50;
}

.task p {
  font-size: 14px;
  color: #7f8c8d;
  margin-bottom: 8px;
}

.task p:last-child {
  margin-bottom: 0;
}

/* Task Completion Checkbox */
.task label {
  display: flex;
  align-items: center;
  gap: 10px;
}

.task input[type="checkbox"] {
  transform: scale(1.3);
  cursor: pointer;
}

/* Task Buttons */
.task button {
  padding: 8px 12px;
  background-color: #e74c3c;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  align-self: flex-start;
  margin-top: 10px;
}

.task button:hover {
  background-color: #c0392b;
}

/* Responsive Design */
@media (max-width: 768px) {
  #content {
      flex-direction: column;
      margin-top: 0;
  }

  #side-bar {
      width: 100%;
      margin-bottom: 20px;
  }

  #taskcontent {
      margin-left: 0;
  }

  button {
      padding: 10px;
      font-size: 14px;
  }
}

